@font-face {
    font-family: 'Open Sans';
    src: url("font/OpenSans-Bold.ttf"),url("font/OpenSans-Medium.ttf"),url("font/OpenSans-Regular.ttf"),url("font/OpenSans-SemiBold.ttf");
}
body{
    font-family: 'Open Sans' !important;
}
header .main-picture{
    width: 100%;
    min-height: 80vh;
    background: url("img/Attack-on-Titan-Saison-4-bande-annonce-date-de-sortie-actualites.jpg") center 10%/cover;
    color: #fff;    
}
.w-c{
    max-width: 600px;
}
select{
    -moz-appearance:none; /* Firefox */
    -webkit-appearance:none; /* Safari and Chrome */
    appearance:none;
    background: url("img/icons8-chevron-down-30.png") no-repeat ;
    background-size: 20px 20px;
    background-position: 90% center;
    padding-right: 50px;
}
option{
    color: #fff;
    background-color: #212529;
    padding: 10px 0;
    font-size: 16px;
}
.star{
    background: url("img/icons8-star-48.png");
    background-position: center center;
    background-size: 50px 50px;
    width: 50px;
    height: 50px;
    display: grid;
    place-items: center;
    font-size: 14px;
    color: black;
    top: -15px;
    left: -5px;
}
